<template>
  <div class='root'>
    <action-bar title='我的' :left="false" :border="false"></action-bar>

    <div class='container'>
      <scroll-view ref="scroll" class="scroll" refresher :data="data" @pullrefresh="onRefresh">
        <div class="content">
          <div class="head">
            <div>
              <img :src="headImage ? headImage : require('@/assets/icon_manager_head_image.png')" class="head-img" />
            </div>
            <div @click="bindAuthResult">
              <div class="head-login-id">{{ data.loginId }}</div>
              <!-- 未认证 -->
              <div v-if="data.state == 0">
                <span class="no-auth">供应商尚未认证</span><span class="result-desc">（认证成功后即可在【易批花】小程序销售商品）</span>
              </div>
              <!-- 审核中 -->
              <div v-if="data.state == 1">
                <span class="authing">供应商审核中</span><span class="result-desc">（认证成功后即可在【易批花】小程序销售商品）</span>
              </div>
              <!-- 认证成功 -->
              <div v-if="data.state == 2">
                <span class="auth-success">供应商已认证</span><span class="result-desc">（可在【易批花】小程序销售商品）</span>
              </div>
              <!-- 认证失败 -->
              <div v-if="data.state == -1">
                <span class="no-auth">供应商认证失败</span><span class="result-desc">（认证成功后即可在【易批花】小程序销售商品）</span>
              </div>
            </div>
            <div v-if="deviceId == ''" class="no-login" @click="bindToLogin">点击登录账户</div>
            <img src="@/assets/arrow_right_icon.png" class="img-right" />
          </div>

          <div class="my-item" @click="bindBusinessDetail">
            <div class="my-item-left">
              <div>
                <img src="@/assets/mine_set_time.png" class="img-info" />
              </div>
              商家信息
            </div>
            <img src="@/assets/arrow_right_icon.png" class="img-right" />
          </div>

          <div class="my-item" @click="bindManagerMineAccount">
            <div class="my-item-left">
              <div>
                <img src="@/assets/icon_mine_account_manager.png" class="img-info" />
              </div>
              账户资金
            </div>
            <img src="@/assets/arrow_right_icon.png" class="img-right" />
          </div>

          <div class="my-item" @click="bindManagerSetting">
            <div class="my-item-left">
              <div>
                <img src="@/assets/manager_mine_setting.png" class="img-info" />
              </div>
              设置
            </div>
            <img src="@/assets/arrow_right_icon.png" class="img-right" />
          </div>
        </div>
      </scroll-view>
    </div>
  </div>
</template>

<script>
import { businessCenter } from '@/http/api'
export default {
  name: 'my',
  data () {
    return {
      data: {},
      headImage: ''
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    onRefresh () {
      this.getData()
    },
    getData () {
      businessCenter().then(res => {
        this.$refs.scroll.refreshed()
        const img = res.data.headImage
        this.data = res.data
        this.headImage = (img !== '') ? this.baseUrl + img : require('@/assets/icon_manager_head_image.png')
      }).catch(res => {
        this.$refs.scroll.refreshed()
        if (res.code === '0003') {
          // this.refresh()
        }
      })
    },
    bindToLogin () {
      this.$router.push('/login')
    },
    bindAuthResult () {

    },
    bindBusinessDetail () {

    },
    bindManagerMineAccount () {

    },
    bindManagerSetting () {
      this.$router.push('/managerSetting')
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  box-sizing: border-box;
  padding-bottom: 3.125rem;
}

.container {
   flex-grow: 1;
   position: relative;
}

.content {
  padding: 1.625rem;
  padding-top: 0;
}

.root {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.head {
  display: flex;
  margin-bottom: .75rem;
  align-items: center;
}

.head-img {
  width: 4.6875rem;
  height: 4.6875rem;
  border-radius: 50%;
  overflow: hidden;
  margin-right: .5rem;
}

.head-login-id {
  font-size: 1rem;
  margin-bottom: .5rem;
  margin-top: .5rem;
}

.result-desc {
  color: #999;
  font-size: .875rem;
}

.no-auth {
  color: #E81A2E;
  font-size: .875rem;
}

.authing {
  color: #FEB63D;
  font-size: .875rem;
}

.auth-success {
  color: #636B82;
  font-size: .875rem;
}

.my-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: 1rem;
  padding: .75rem 0;
}

.my-item-left {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.img-info {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: .5rem;
}

.img-right {
  height: auto;
  width: .4375rem;
  vertical-align: middle;
}

.no-login {
  font-size: 1.125rem;
  padding: .75rem;
  color: #666;
}
</style>
